.floating-heart {
  position: absolute;
  animation: float 6s ease-in-out infinite;
}
.petal {
  position: fixed;
  width: 15px;
  height: 15px;
  background: linear-gradient(135deg, #fecdd3 0%, #fda4af 100%);
  border-radius: 150px 0px;
  transform: rotate(45deg);
  animation: petal-fall linear infinite;
  pointer-events: none;
  z-index: 1000;
}
.glass {
  background: rgba(255, 255, 255, 0.25);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.18);
}
.romantic-shadow {
  text-shadow: 0 0 30px rgba(225, 29, 72, 0.3), 0 0 60px rgba(225, 29, 72, 0.2);
}
.gradient-text {
  background: linear-gradient(135deg, #be123c 0%, #e11d48 50%, #f43f5e 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}
.glow-button {
  box-shadow: 0 0 20px rgba(225, 29, 72, 0.4), 0 0 40px rgba(225, 29, 72, 0.2);
  transition: all 0.3s ease;
}
.glow-button:hover {
  box-shadow: 0 0 30px rgba(225, 29, 72, 0.6), 0 0 60px rgba(225, 29, 72, 0.3);
  transform: translateY(-2px);
}
.shimmer {
  position: relative;
  overflow: hidden;
}
.shimmer::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    90deg,
    transparent,
    rgba(255, 255, 255, 0.4),
    transparent
  );
  animation: shimmer 3s infinite;
}
@keyframes shimmer {
  0% {
    left: -100%;
  }
  100% {
    left: 100%;
  }
}
